import { Component, OnInit, ViewEncapsulation, ElementRef, ViewChild, NgZone, AfterViewInit, ChangeDetectorRef, EventEmitter, Output } from '@angular/core';
import { TweenMax, TimelineMax, Power1, Back, Power3 } from 'gsap';


@Component({
  selector: 'tpc-tree',
  templateUrl: './tree.component.html',
  styleUrls: ['./tree.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class TreeComponent implements OnInit {


  @ViewChild('g1') g1: ElementRef;
  @ViewChild('g2') g2: ElementRef;
  @ViewChild('g3') g3: ElementRef;
  @ViewChild('g4') g4: ElementRef;
  @ViewChild('splashLines') splashLines: ElementRef;
 
  timeLine: TimelineMax;

  @Output() completed = new EventEmitter<boolean>();

    constructor(
    private zone: NgZone,
    private cdr: ChangeDetectorRef
  ) {
    this.timeLine = new TimelineMax({ repeat: 5, delay: 1, yoyo: false, paused: true });
    this.timeLine.eventCallback("onComplete", () => this.completed.emit(true));
  }
  ngOnInit() {
  

    const a = this.g1.nativeElement;
    const b = this.g2.nativeElement;
    const c = this.g3.nativeElement;
    const d = this.g4.nativeElement;
    const splashLines = this.splashLines.nativeElement;


  

    const B = this.timeLine;


    TweenMax.set([a,b,c,d], {
      transformOrigin: "50% 50%"
    });
    B.timeScale(3);
    B.to(splashLines, 0.8, {
      drawSVG:0,
      ease: Power3.easeOut,
      alpha:1
    }, '-=0.2')
    
    .to(splashLines, 0.8, {
      
      alpha:0
    }, '-=0.65')

  }

  ngAfterViewInit() {
    this.cdr.detach();
    this.startAnimate();
  }

  startAnimate() {
    this.zone.runOutsideAngular(() => this._animate());
  }

  private _animate() {
    TweenMax.set("svg", { visibility: "hidden" });

    this.timeLine.play(0);
    TweenMax.set("svg", { visibility: "visible" });
  }


}
